.input-dm,
.textarea-dm {
  height: 32px;
  width: 100%;
  background: #FAFAFA;
  font-size: 14px;
  border: 1px solid var(--sand_300);
  box-sizing: border-box;
  border-radius: 5px;
  padding: 0 16px;
  transition: box-shadow 80ms ease;
}

.input-dm {
  &_size {
    &_compact {
      height: 32px;
    }

    &_small {
      height: 24px;
      font-size: 12px;
      padding: 0 5px;
    }

    &_large {
      height: 40px;
      font-size: 16px;
    }
  }
}

.textarea-dm {
  padding: 12px 16px;
  min-height: 50px;
}

.input-dm:focus,
.textarea-dm:focus {
  outline: none;
  box-shadow: 0px 0px 0px 6px rgba(var(--accent_color-raw), 0.2), inset 0px -1px 0px var(--black_10), inset 0px 0px 0px 1px var(--black_15), inset 0px 0px 0px 1px rgba(var(--accent_color-raw), 0.2);
  border-color: rgba(var(--accent_color-raw), 0.2);
}
